@import '../../../../mixins';

.techs {
    &__container {
        align-items: flex-end;

        @include respond (ipad) {
            & { 
                align-items: flex-start;
            }
        }

        & i {
            font-size: 5rem;
            margin-right: 2.5rem;

            @include respond (ipad) {
                & { 
                    margin-bottom: 2rem;
                }
            }
        }

        @include respond (phone) {
            & {
                display: flex;
                flex-flow: column nowrap;
                justify-content: center;
                align-items: center;
                width: 100%;
            }
        }
    }
    
    &__icons {
        display: flex;
        flex-flow: row nowrap;
        width: 100%;
        overflow: hidden;

        @include respond (large-tab-portrait) {
            & { 
                justify-content: center;
                align-items: center;
                width: 100%;
                flex-wrap: wrap;
                padding: 1rem 2rem;
            }
        }

        @include respond (ipad) {
            & { 
                justify-content: space-between;
                align-items: center;
                width: 100%;
                flex-wrap: wrap;
                padding: 1rem 2rem;
            }
        }

        @include respond (phone) {
            & {
                display: flex;
                flex-flow: row nowrap;
                justify-content: flex-start;
                align-items: center;
                overflow: scroll;
                width: 100%;
                scrollbar-width: none;
                -ms-overflow-style: none;

                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }
    }
}