@import '../../../colors';
@import '../../../mixins';

.project {
    &__container {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-evenly;
        width: 100%;
        height: 45%;

        @include respond (large-tab-portrait) {
            & {
                height: 55%;
                flex-flow: column nowrap;
            }
        }

        @include respond (ipad) {
            & {
                height: 55%;
                flex-flow: column nowrap;
            }
        }

        @include respond (phone) {
            & { 
                width: 90%;
                flex-flow: column nowrap;
                height: fit-content;
                justify-content: center;
                align-items: center;
            }
        }
    }
}