@import '../../../../colors';
@import '../../../../mixins';

.list-item {    

    &__container {
        display: flex;
        flex-flow: column nowrap;
        justify-content: space-around;
        padding: 2rem 0 0 0;
        margin-right: 6rem;
        opacity: 1;
        transition: all .7s;
        -webkit-transition: all .7s;
        -moz-transition: all .7s;
        -ms-transition: all .7s;
        -o-transition: all .7s;

        &.fade {
            opacity: 0;
            transition: all .7s;
            -webkit-transition: all .7s;
            -moz-transition: all .7s;
            -ms-transition: all .7s;
            -o-transition: all .7s;
        }

        & h4 {
            margin-bottom: 0;
            margin-top: 1rem;
            padding: 0;
            word-wrap: break-word;
            max-width: 12.5rem;
        }
    }

    &__thumbnail {
        width: 12.5rem;
        height: 12.5rem;
        background: $main-gradient;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        box-shadow: .5rem .5rem 1.5rem .7rem rgba(0, 0, 0, 0.3);
        transition: all 1s;
        -webkit-transition: all 1s;
        -moz-transition: all 1s;
        -ms-transition: all 1s;
        -o-transition: all 1s;
        overflow: hidden;
        position: relative;
        opacity: 0.5;

        &:hover {
            opacity: 1;
        }

        & .thumbnail__img-container {
            height: 11.5rem;
            width: 11.5rem;
            overflow: hidden;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
            -moz-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
            -o-transform: translate(-50%, -50%);
            backface-visibility: hidden;
            -webkit-backface-visibility: hidden;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;

            & img {
                max-height: 100%;
                transition: all 1s;
                -webkit-transition: all 1s;
                -moz-transition: all 1s;
                -ms-transition: all 1s;
                -o-transition: all 1s;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                -webkit-transform: translate(-50%, -50%);
                -moz-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                -o-transform: translate(-50%, -50%);
                transform-origin: center;
                backface-visibility: hidden;
                -webkit-backface-visibility: hidden;
            }
        }
        
        &:hover img {
            transform: scale(1.1) translate(-50%, -50%);
            -webkit-transform: scale(1.1) translate(-50%, -50%);
            -moz-transform: scale(1.1) translate(-50%, -50%);
            -ms-transform: scale(1.1) translate(-50%, -50%);
            -o-transform: scale(1.1) translate(-50%, -50%);
            cursor: pointer;
        }
    }
}