@import '../../../../colors';
@import '../../../../mixins';

.project__mockup-container {
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    margin-right: 5%;

    @include respond (hide-nav) {
        & {
            justify-content: flex-start;
        }
    }

    @include respond (tab-port) {
        & {
            justify-content: center;
            margin-right: 0;
        }
    }
    
    @include respond (large-tab-portrait) {
        & {
            margin-right: 0;
        }
    }

    @include respond (phone) {
        & {
            margin-right: 0;
            margin-bottom: 5rem;
        }
    }

    & .project__mockup {
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
        width: 27vw;
        height: 19vw;
        text-align: center;
        background: $main-gradient;
        border-radius: 1rem;
        -webkit-border-radius: 1rem;
        -moz-border-radius: 1rem;
        -ms-border-radius: 1rem;
        -o-border-radius: 1rem;
        box-shadow: .5rem .5rem 1rem .7rem rgba(0, 0, 0, 0.25);

        @include respond (hide-nav) {
            & {
                width: 33vw;
                height: 21vw;
            }
        }

        @include respond (tab-land) {
            & {
                width: 36vw;
                height: 24vw;
                margin-bottom: 2rem;
            }
        }

        @include respond (large-tab-portrait) {
            & {
                width: 50vw;
                height: 36vw;
            }
        }
    
        @include respond (tab-port) {
            & {
                width: 39vw;
                height: 27vw;
            }
        }

        @include respond (ipad) {
            & {
                margin-top: 4rem;
                margin-bottom: 4rem;
                width: 45vw;
                height: 30vw;
            }
        }
    
        @include respond (phone) {
            & {
                margin-bottom: 0;
                width: 75vw;
                height: 45vw;
            }
        }

        @include respond (small-phone) {
            & {
                margin-top: 0;
            }
        }
        
        & a {
            width: 97%;
            height: 96%;
            overflow: hidden;
            border-radius: 1rem;
            -webkit-border-radius: 1rem;
            -moz-border-radius: 1rem;
            -ms-border-radius: 1rem;
            -o-border-radius: 1rem;
            position: relative;
            background: $light-gray;
        }
    
        &-image {
            width: 90%;
            height: auto;
            position: absolute;
            top: 50%;
            left: 50%;
            border-radius: 1rem;
            -webkit-border-radius: 1rem;
            -moz-border-radius: 1rem;
            -ms-border-radius: 1rem;
            -o-border-radius: 1rem;
            transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
            -moz-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
            -o-transform: translate(-50%, -50%);

            @include respond (phone) {
                & {
                    height: 90%;
                    width: auto;
                }
            }
        }
    }

    .project__mockup-links {
        display: flex;
        width: 100%;
        flex-flow: row nowrap;
        justify-content: space-evenly;
        align-items: center;
        margin-top: 1.5rem;

        & a {
            text-decoration: none;
            background: $app-boxes;
            color: $light-gray;
            border: 2px solid $light-gray;
            padding: .5rem 1rem;
            font-weight: 500;
            width: fit-content;
            border-radius: 2rem;
            -webkit-border-radius: 2rem;
            -moz-border-radius: 2rem;
            -ms-border-radius: 2rem;
            -o-border-radius: 2rem;
            box-shadow: .3rem .3rem .6rem .6rem rgba(0, 0, 0, 0.25);

            &:hover {
                color: $app-background;
                background: $light-gray;
            }
        }
    }

    & i {
        font-size: 2rem;

        @include respond (phone) {
            & {
               font-size: 1.7rem;             
            }
        }
    }

    @include respond (large-tab-portrait) {
        & {
            align-self: center;
            height: 60%;
            width: 65%;
            margin-bottom: 60px;
        }
    }

    @include respond (ipad) {
        & {
            align-self: center;
            height: 60%;
            width: 65%;
            margin-bottom: 60px;
        }
    }

    @include respond (phone) {
        & {
            justify-content: center;
            width: 100%;
            height: 50%;
            margin-bottom: 3rem;

            & .project__mockup {

                &-links {
                    & a {
                        border: 1px solid $light-gray;
                        font-weight: 400;
                        margin-top: 1rem;
                    }
                }
            }
        }
    }
}

