@import '../../keyframes'; 
@import '../../mixins';

.my-works__container {
    width: 90%;
    height: 90%;
    animation-name: enter;
    animation-duration: 2s;
    animation-timing-function: ease-out;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;

    & h1 {
        margin-bottom: 4rem;
    }

    @include respond (phone) {
        & {
            width: 100%;
            height: 100%;
            margin: 0 auto;
            overflow-y: scroll;
            scrollbar-width: none;
            -ms-overflow-style: none;

            &::-webkit-scrollbar {
                display: none;
            }
        }
    }
}