@import '../../keyframes';
@import '../../mixins';

.contact__container {
    width: 80%;
    height: 90%;
    animation-name: enter;
    animation-duration: 2s;
    animation-timing-function: ease-out;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;

    @include respond (phone) {
        & {
            width: 100%;
            height: 100%;
            justify-content: flex-start;
            margin-bottom: 5rem;
        }
    }

    & h1 {
        margin-top: 0;

        @include respond (phone) {
            & {
                margin-top: 5rem;
            }
        }
    }

    & button {
        box-shadow: .7rem .7rem 1.4rem .8rem rgba(0, 0, 0, 0.3);
        height: fit-content;
        padding: 1rem 2rem;

        @include respond (phone) {
            & {
                padding: 2rem;
                line-height: 0;
            }
        }
    }
}