@import '../../keyframes';
@import '../../mixins';
@import '../../colors';

.blog__container {
    height: 90vh;
    width: 100%;
    position: relative;
    animation-name: enter;
    animation-duration: 2s;
    animation-timing-function: ease-out;
    z-index: 1;

    & .text__container {

        position: absolute;
        top: 15%;
        left: 10%;
        height: 85%;
        width: 85%;

        & h1 {
            font-size: 15rem;
            line-height: 15rem;
            margin: 0;
            padding: 0;
            font-family: 'Roboto Mono', monospace;
            z-index: 10;

            &.first {
                position: absolute;
                bottom: 80%;
                left: 0;
                -webkit-text-stroke: 2px $light-gray;
                opacity: 0.6;
                color: $app-boxes;
            }

            &.second {
                position: absolute;
                top: 19%;
                left: 8%;
            }

            &.third {
                position: absolute;
                bottom: 50%;
                right: 0%;
                -webkit-text-stroke: 2px $light-gray;
                opacity: 0.6;
                color: $app-boxes;
            }

            @include respond (large-tab-portrait) {
                
                &.first {
                    bottom: 82%;
                    left: -5%;
                }

                &.second {
                    bottom: 80%;
                    left: 0;
                }

                &.third {
                    bottom: 60%;
                    right: -5%;
                }
            }
    
            @include respond (ipad) {
                font-size: 12rem;
                line-height: 12rem;

                &.first {
                    bottom: 82%;
                    left: -5%;
                }

                &.second {
                    bottom: 80%;
                    left: 0;
                }

                &.third {
                    bottom: 60%;
                    right: -5%;
                }
            }

            @include respond (tab-land) {
                font-size: 13rem;
                line-height: 13rem;
                
                &.first {
                    bottom: 82%;
                    left: -5%;
                }

                &.second {
                    bottom: 80%;
                    left: 0;
                }

                &.third {
                    bottom: 50%;
                    right: -5%;
                }
            }

            @include respond (tab-port) {
                font-size: 12rem;
                line-height: 12rem;
                
                &.first {
                    bottom: 82%;
                    left: -5%;
                }

                &.second {
                    bottom: 80%;
                    left: 0;
                }

                &.third {
                    bottom: 62%;
                    right: -5%;
                }
            }
    
            @include respond (phone) {
                font-size: 6.5rem;
                line-height: 6.5rem;
                
                &.first {
                    bottom: 81%;
                    left:-9%;
                }

                &.second {
                    bottom: 80%;
                    left: -5%;
                }

                &.third {
                    bottom: 65%;
                    right: -5%;
                }
            }

            @include respond (small-phone) {
                font-size: 6rem;
                line-height: 6rem;
                
                &.first {
                    bottom: 80%;
                    left:-8%;
                    -webkit-text-stroke: 1px $light-gray;
                }

                &.second {
                    bottom:58%;
                    left: -5%;
                }

                &.third {
                    bottom: 67%;
                    right: -5%;
                    -webkit-text-stroke: 1px $light-gray;
                }
            }
        }
    }

    &::before {
        content: "";
        background: $app-background;
        background-size: cover;
        opacity: 0.7;
        top: -15%;
        left: 0;
        bottom: -5%;
        right: 0;
        position: absolute;
        z-index: -1;
        border-radius: 1rem;
        -webkit-border-radius: 1rem;
        -moz-border-radius: 1rem;
        -ms-border-radius: 1rem;
        -o-border-radius: 1rem;
    }

    &::after {
        content: "";
        background: url(../../assets/images/computer-chair.webp);
        background-size: cover;
        background-position-x: 40%;
        opacity: 0.4;
        top: -15%;
        left: 0;
        bottom: -5%;
        right: 0;
        position: absolute;
        z-index: -2;
        border-radius: 1rem;
        -webkit-border-radius: 1rem;
        -moz-border-radius: 1rem;
        -ms-border-radius: 1rem;
        -o-border-radius: 1rem;


        @include respond (large-tab-portrait) {
            & {
                background-position-x: 60%;
            }
        }

        @include respond (ipad) {
            & {
                background-position-x: 55%;
            }
        }

        @include respond (phone) {
            & {
              background-position-x: 60%;
            }
        }
    }
}