@import '../../../../mixins';

.description {

    &__container {
        width: 50%;
        height: 100%;
        text-align: justify;
        display: flex;
        flex-flow: column nowrap;

        @include respond (large-tab-portrait) {
            & {
                align-self: center;
                height: 25%;
                width: 90%;
                align-items: center;
                flex-flow: row nowrap;
                justify-content: space-evenly;
                text-align: center;

                & p {
                    text-align: justify;
                }
            }
        }

        @include respond (ipad) {
            & {
                align-self: center;
                height: 25%;
                width: 100%;
                align-items: flex-start;
                flex-flow: row nowrap;
                justify-content: space-evenly;
                text-align: center;

                & p {
                    text-align: justify;
                }
            }
        }

    }
    
    &__text {
        height: 75%;

        @include respond (large-tab-portrait) {
            & {
                width: 50%;
            }
        }

        @include respond (ipad) {
            & {
                width: 50%;
                margin-right: 5rem;
            }
        }

        & h3 {
            padding: 0;
            margin-top: 0;
            margin-bottom: 5rem;

            @include respond (phone) {
                & {
                    margin-bottom: 2rem;
                }
            }
        }
    }

    &__techs {
        height: 25%;
        justify-self: flex-end;
        display: flex;
        flex-flow: row nowrap;
        align-items: flex-end;

        @include respond (large-tab-portrait) {
            & {
                align-items: center;
                align-self: center;
                justify-self: flex-end;
                width: 50%;
            }
        }

        @include respond (ipad) {
            & {
                align-items: center;
                align-self: center;
                justify-self: flex-end;
                width: 50%;
            }
        }
    }

    @include respond (phone) {
        &__container {
            width: 90%;
            text-align: center;
        }

        &__text {

            & h3 {
                text-align: center;
            }
            
            text-align: justify;
        }
    }
}